// app/page.tsx
import { CloudCog } from "lucide-react";
import React, { useState } from "react";

// Custom Input Component
const Input = ({ placeholder, value, onChange, className }: {
  placeholder?: string;
  value: string;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  className?: string;
}) => (
  <input
    type="text"
    placeholder={placeholder}
    value={value}
    onChange={onChange}
    className={`w-full p-3 rounded-lg border border-gray-300 bg-gray-800 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 transition duration-200 ${className}`}
  />
);

// Custom Button Component
const Button = ({ children, onClick, disabled, className }: {
  children: React.ReactNode;
  onClick: () => void;
  disabled?: boolean;
  className?: string;
}) => (
  <button
    onClick={onClick}
    disabled={disabled}
    className={`w-full sm:w-auto px-6 py-3 rounded-lg bg-gradient-to-r from-blue-600 to-purple-600 text-white font-semibold hover:from-blue-700 hover:to-purple-700 disabled:opacity-50 disabled:cursor-not-allowed transition duration-200 ${className}`}
  >
    {children}
  </button>
);

// Custom Card Components
const Card = ({ children, className }: { children: React.ReactNode; className?: string }) => (
  <div className={`bg-gray-800 rounded-lg shadow-lg p-6 ${className}`}>
    {children}
  </div>
);

const CardContent = ({ children, className }: { children: React.ReactNode; className?: string }) => (
  <div className={`space-y-4 ${className}`}>
    {children}
  </div>
);

export default function Home() {
  const [url, setUrl] = useState("");
  const [result, setResult] = useState<any>(null);
  const [loading, setLoading] = useState(false);

  const handleScrape = async () => {
    setLoading(true);
    try {
      const res = await fetch("/api/scrape/add", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ url, scraper_type: "beautifulsoup" }),
      });
      const {data}= await res.json();
      console.log(data)
      setResult(data);
    } catch (error) {
      console.error("Scrape failed", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-900 to-gray-800 py-10 px-4 sm:px-8 md:px-16 flex items-center justify-center">
      <div className="max-w-4xl w-full mx-auto space-y-8">
        {/* Header Section */}
        <div className="text-center">
          <h1 className="text-4xl sm:text-5xl font-bold text-white tracking-tight">
            ðŸ§  AI Web Scraper
          </h1>
          <p className="mt-2 text-gray-300 text-lg sm:text-xl">
            Paste any article or website URL to extract content and generate AI-powered insights like summaries, sentiment, and SEO tags.
          </p>
        </div>

        {/* Input and Button Section */}
        <div className="flex flex-col sm:flex-row gap-4">
          <Input
            placeholder="https://example.com/article"
            value={url}
            onChange={(e) => setUrl(e.target.value)}
            className="flex-grow"
          />
          <Button onClick={handleScrape} disabled={loading}>
            {loading ? (
              <span className="flex items-center gap-2">
                <svg className="animate-spin h-5 w-5 text-white" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8v8z" />
                </svg>
                Scraping...
              </span>
            ) : (
              "Start Scraping"
            )}
          </Button>
        </div>

        {/* Results Section */}
        {result && (
          <Card className="mt-8">
            <CardContent>
              <h2 className="text-2xl font-semibold text-white">Results</h2>
              <div className="space-y-4 text-gray-200">
                <div>
                  <strong className="text-blue-400">Headline:</strong>{" "}
                  {result?.data?.headline || "-"}
                </div>
                <div>
                  <strong className="text-blue-400">TL;DR:</strong>{" "}
                  {result?.data?.tldr || "-"}
                </div>
                <div>
                  <strong className="text-blue-400">Summary:</strong>
                  <p className="text-sm text-gray-300 whitespace-pre-wrap">
                    {result?.data?.summary || "-"}
                  </p>
                </div>
                <div>
                  <strong className="text-blue-400">Topics:</strong>{" "}
                  {result?.data?.topics?.join(", ") || "-"}
                </div>
                <div>
                  <strong className="text-blue-400">Sentiment:</strong>{" "}
                  {result?.data?.sentiment?.label} (
                  {result?.data?.sentiment?.score || "-"})
                </div>
                <div>
                  <strong className="text-blue-400">SEO Tags:</strong>{" "}
                  {result?.data?.seo_tags?.join(", ") || "-"}
                </div>
              </div>
            </CardContent>
          </Card>
        )}
      </div>
    </main>
  );
}